<template>
<div class="second_part">
	<header class="part_headers">
		<h2>
			第二部分
		</h2>
		<!-- <input type="button" value="测试"> -->
		<div class="currentpage">
			<div class="textpage">当前 {{currentpage}}/{{allpage}}页</div>
			<div class="progress">
				<span v-for="page in allpage" :key="page" :class="addcolor(page)"></span>
			</div>
		</div>
	</header>
	<section class="question_wrap" ref="question_wrap">
		<ol class="part1" v-if="currentpage==1">
			<li>
				<h4>
					1.请问您所在办公室的种类？
				</h4>
				<el-radio-group v-model="forms.question1">
					<el-radio v-for="(item,index) in question1" :key="index" :label="item">{{item[index+1]}}</el-radio>
				</el-radio-group>
			</li>
			<li>
				<h4>2.请填写您一般待在办公室的作息时间</h4>
				<div class="selectTime" @click="selectTime">
					<span>{{forms.question2||'--:-- 至 --:--'}}</span>
				</div>
				<mt-popup v-model="istimeselectshow" position="bottom" class="selecttimes">
					<div class="selecttimetool">
						<span @click.prevent="cancelToSelect">取消</span>
						<span @click.prevent="sureToSelect">确定</span>
					</div>
					<mt-picker :slots="timeselects" @change="onDateChange" :visible-item-count="5" :show-toolbar="false"></mt-picker>
				</mt-popup>
			</li>
			<li>
				<h4>3.在刚刚过去的供冷季里（6-8月），您在室内若感觉偏冷，会采取哪些措施缓解？（多选）</h4>
				<el-checkbox-group v-model="forms.question3">
					<el-checkbox v-if="index !== question3.length -1" v-for="(item,index) in question3" :key="index" :label="item">{{item[index+1]}}</el-checkbox>
					<el-checkbox @change="other" v-else-if="index == question3.length -1" :label="item">其他<input type="text" class="other" name="3" v-model="item[0]" />
					</el-checkbox>
				</el-checkbox-group>
			</li>
			<li>
				<h4>4.在刚刚过去的供冷季里（6-8月），您在室内若感觉偏热，会采取哪些措施缓解？（多选）</h4>
				<el-checkbox-group v-model="forms.question4">
					<el-checkbox v-if="index !== question4.length -1" v-for="(item,index) in question4" :key="index" :label="item">{{item[index+1]}}</el-checkbox>
					<el-checkbox @change="other" v-else-if="index == question4.length -1" :label="item">其他<input type="text" class="other" name="4" v-model="item[0]" />
					</el-checkbox>
				</el-checkbox-group>
			</li>
		</ol>
		<dl class="part2" v-else-if="currentpage==2">
			<dt>【请回想使用微信反馈控制空调的场景】</dt>
			<dd>
				<h4>1.在刚刚过去的供冷季里，您是否经常使用朵朵APP调节办公室空调？</h4>
				<el-radio-group v-model="forms.question5">
					<el-radio v-for="(item,index) in question5" :key="index" :label="item">{{item[index+1]}}</el-radio>
				</el-radio-group>
			</dd>
			<dd>
				<h4>2.在刚刚过去的供冷季里，你为什么频繁使用朵朵APP调节空调？</h4>
				<el-radio-group v-model="forms.question6">
					<el-radio v-for="(item,index) in question6" :key="index" :label="item">{{item[index+1]}}</el-radio>
				</el-radio-group>
			</dd>
			<dd>
				<h4>3.在刚刚过去的供冷季里，你为什么很少使用朵朵APP调节空调？</h4>
				<el-radio-group v-model="forms.question7">
					<el-radio v-for="(item,index) in question7" :key="index" :label="item">{{item[index+1]}}</el-radio>
				</el-radio-group>
			</dd>
		</dl>
		<dl class="part3" v-else-if="currentpage==3">
			<dt>【请回想使用微信反馈控制空调的场景】</dt>
			<dd>
				<h4>假设你在采用控制面板调节空调的单人办公室中工作， 你在供冷季会如何使用办公室空调？
				</h4>
				<p class="part_open">开启方式（可多选）</p>
				<el-checkbox-group v-model="forms.question8">
					<el-checkbox v-if="index !== question8.length -1&&index!=2" v-for="(item,index) in question8" :key="index" :label="item">{{item[index+1]}}</el-checkbox>

					<el-checkbox class="otherwrap" @change="selectsShow(8,$event)" v-else-if="index == 2" :label="item">
						{{item[index+1]}}
					</el-checkbox>

					<el-checkbox class="otherwrap" @change="other" v-else-if="(index == (question8.length -1))" :label="item">
						其他
						<input type="text" class="other" name="8" v-model="item[0]" />
					</el-checkbox>

				</el-checkbox-group>
			</dd>
			<dd>
				<p class="part_close">关闭方式（可多选）</p>
				<el-checkbox-group v-model="forms.question9">
					<el-checkbox v-if="index !== question9.length -1&&index!=2" v-for="(item,index) in question9" :key="index" :label="item">{{item[index+1]}}</el-checkbox>

					<el-checkbox class="otherwrap" @change="selectsShow(9,$event)" v-else-if="index == 2" :label="item">
						{{item[index+1]}}
					</el-checkbox>

					<el-checkbox @change="other" v-else-if="index == question9.length -1" :label="item">其他<input type="text" class="other" name="9" v-model="item[0]" />
					</el-checkbox>
				</el-checkbox-group>
			</dd>
			<dd>
				<p class="part_type">温度设定方式 （单选）</p>
				<el-radio-group v-model="forms.question10" @change="otherradio($event)">
					<el-radio v-for="(item,index) in question10" :key="index" :label="item" v-if="index!=question10.length-1">{{item[index+1]}}</el-radio>

					<el-radio v-else @change="otherradio" :label="question10[3]">其他<input type="text" class="other" name="9" v-model="question10[3][0]" />
					</el-radio>

				</el-radio-group>
			</dd>
			<mt-popup v-model="istimeselectshow" position="bottom" class="selecttimes" :closeOnClickModal="false">
				<div class="selecttimetool">
					<span @click.prevent="cancelpart3">取消</span>
					<span @click.prevent="surepart3">确定</span>
				</div>
				<mt-picker :slots="TimeOrWendu" @change="onTimeChange" :visible-item-count="5" :show-toolbar="false"></mt-picker>
			</mt-popup>
			<mt-popup v-model="TwoSelects" position="bottom" class="selecttimes" :closeOnClickModal="false">
				<div class="selecttimetool">
					<span @click.prevent="cancelpart3">取消</span>
					<span @click.prevent="surepart3">确定</span>
				</div>
				<mt-picker :slots="TwoWenDu" @change="onWenduChange" :visible-item-count="5" :show-toolbar="false"></mt-picker>
			</mt-popup>
		</dl>
		<dl class="part4" v-else-if="currentpage==4">
			<dt>【请回想使用微信反馈控制空调的场景】</dt>
			<dd>
				<h4>如果在采用空调面板调节的多人办公室中，你会如何调节空调？</h4>
				<p class="part_open">开启方式（可多选）</p>
				<el-checkbox-group v-model="forms.question11">
					<el-checkbox v-if="index !== question11.length -1&&index!=2" v-for="(item,index) in question11" :key="index" :label="item">{{item[index+1]}}</el-checkbox>
					<el-checkbox class="otherwrap" @change="selectsShow(11,$event)" v-else-if="index == 2" :label="item">
						{{item[index+1]}}
					</el-checkbox>

					<el-checkbox class="otherwrap" @change="other" v-else-if="(index == (question11.length -1))" :label="item">
						其他
						<input type="text" class="other" name="11" v-model="item[0]" />
					</el-checkbox>
				</el-checkbox-group>
			</dd>
			<dd>
				<p class="part_close">关闭方式（可多选）</p>
				<el-checkbox-group v-model="forms.question12">
					<el-checkbox v-if="index !== question12.length -1&&index!=2" v-for="(item,index) in question12" :key="index" :label="item">{{item[index+1]}}</el-checkbox>

					<el-checkbox class="otherwrap" @change="selectsShow(12,$event)" v-else-if="index == 2" :label="item">
						{{item[index+1]}}
					</el-checkbox>

					<el-checkbox @change="other" v-else-if="index == question12.length -1" :label="item">其他<input type="text" class="other" name="12" v-model="item[0]" />
					</el-checkbox>
				</el-checkbox-group>
			</dd>
			<dd>
				<p class="part_type">温度设定方式 （单选）</p>
				<el-radio-group v-model="forms.question13" @change="otherradio($event)">
					<el-radio v-for="(item,index) in question13" :key="index" :label="item" v-if="index!=question13.length-1">{{item[index+1]}}</el-radio>

					<el-radio v-else @change="otherradio" :label="question13[5]">其他<input type="text" class="other" name="13" v-model="question13[5][0]" />
					</el-radio>

				</el-radio-group>
			</dd>
			<mt-popup v-model="istimeselectshow" position="bottom" class="selecttimes" :closeOnClickModal="false">
				<div class="selecttimetool">
					<span @click.prevent="cancelpart3">取消</span>
					<span @click.prevent="surepart3">确定</span>
				</div>
				<mt-picker :slots="TimeOrWendu" @change="onTimeChange" :visible-item-count="5" :show-toolbar="false"></mt-picker>
			</mt-popup>
			<mt-popup v-model="TwoSelects" position="bottom" class="selecttimes" :closeOnClickModal="false">
				<div class="selecttimetool">
					<span @click.prevent="cancelpart3">取消</span>
					<span @click.prevent="surepart3">确定</span>
				</div>
				<mt-picker :slots="TwoWenDu" @change="onWenduChange" :visible-item-count="5" :show-toolbar="false"></mt-picker>
			</mt-popup>
		</dl>

	</section>
	<footer class="setStep">
		<div class="previousstep" v-if="currentpage!=1" @click="previousstep">
			上一页
		</div>
		<div class="nextstep" :class="toggleClass?'yes':'no'" @click="nextstep">
			{{currentpage==allpage?'完成':'下一页'}}
		</div>
	</footer>
</div>
</template>
<script>
import tools from 'utils/tools'
import router from 'src/router'
import BScroll from 'better-scroll'
import {
	addSecQuestionnaireAnswer
} from 'api/question'
import {
	question1,
	question2,
	question3,
	question4,
	question5,
	question6,
	question7,
	question8,
	question9,
	question10,
	question11,
	question12,
	question13
} from './questions'
import {
	DatetimePicker,
	Indicator,
	Toast,
	MessageBox,
	Popup,
	Picker
} from 'mint-ui';
export default {
	name: 'secondpart',
	data: function() {
		let openId = tools.getCookie('openId')
		return {
			openId,
			question1,
			question2,
			question3,
			question4,
			question5,
			question6,
			question7,
			question8,
			question9,
			question10,
			question11,
			question12,
			question13,
			currentpage: 1,
			allpage: 4,
			forms: {
				question1: null,
				question2: null,
				question3: [],
				question4: [],
				question5: null,
				question6: null,
				question7: null,
				question8: [],
				question9: [],
				question10: null,
				question11: [],
				question12: [],
				question13: null
			},
			istimeselectshow: false, //选择时间的picker是否展示出来
			//第二题选择时间
			userSelecting: '', //用户选择的时间
			timeselects: [{
				flex: 1,
				values: [],
				className: 'hours',
				textAlign: 'right'
			}, {
				divider: true, //对应 slot 是否为分隔符
				content: '-', //分隔符 slot 的显示文本
				className: 'slot-H'
			}, {
				flex: 1,
				values: [],
				className: 'minutes',
				textAlign: 'left'
			}, {
				divider: true,
				content: '至',
				className: 'slot-Z'
			}, {
				flex: 1,
				values: [],
				className: 'hours',
				textAlign: 'right'
			}, {
				divider: true,
				content: '-',
				className: 'slot-H'
			}, {
				flex: 1,
				values: [],
				className: 'minutes',
				textAlign: 'left'
			}],
			//第8题选择时间
			TimeOrWendu: [{
				flex: 1,
				values: [],
				className: 'hours',
				textAlign: 'center'
			}],
			TwoWenDu: [{
				flex: 1,
				values: [],
				className: 'hours',
				textAlign: 'right'
			}, {
				divider: true, //对应 slot 是否为分隔符
				content: '-', //分隔符 slot 的显示文本
				className: 'slot-H',
			}, {
				flex: 1,
				values: [],
				className: 'minutes',
				textAlign: 'left'
			}],
			TwoSelects: false, //选择两个温度的
			showWhich: 8, //保存选择的是第几题
			values: '', //保存选择的内容
		}
	},
	watch: {
		forms: {
			deep: true,
			handler: function(val) {
				tools.setStorage(`${this.openId}_partTwo`, val)
			}
		},
	},
	computed: {
		toggleClass() {
			if (this.currentpage == 1) {
				return this.calcForm([1, 4])
			} else if (this.currentpage == 2) {
				return this.calcForm([5, 7])
			} else if (this.currentpage == 3) {
				return this.calcForm([8, 10])
			} else if (this.currentpage == 4) {
				return this.calcForm([11, 13])
			}
		},
	},
	methods: {
		//第三部分的picker选择
		surepart3() { //确定按钮
			if (this.showWhich == 8 || this.showWhich == 9 || this.showWhich == 11 || this.showWhich == 12) {
				//第8题和第9题\第11题和第12题
				let arr = this.forms['question' + this.showWhich]
				arr[arr.length - 1][3] = (this.showWhich == 8 || this.showWhich == 11) ? `定时${this.values}点开；` : `定时${this.values}关；`

			} else if (this.showWhich == 101) {
				this.question10[0][1] = `固定设定值，一直是${this.values}；`
			} else if (this.showWhich == 102) {
				this.question10[1][2] = `开机时设为${this.values[0]}，觉得冷后调到${this.values[1]}；`
				this.TwoSelects = false;
			} else if (this.showWhich == 103) {
				this.question10[2][3] = `人在房间设为${this.values[0]}，不在房间调到${this.values[1]}；`
				this.TwoSelects = false;
			} else if (this.showWhich == 131) {
				this.question13[0][1] = `固定设定值，一直是${this.values}；`
			} else if (this.showWhich == 132) {
				this.question13[1][2] = `开机时设为${this.values[0]}，觉得冷后调到${this.values[1]}；`
				this.TwoSelects = false;
			} else if (this.showWhich == 133) {
				this.question13[2][3] = `人在房间设为${this.values[0]}，不在房间调到${this.values[1]}；`
				this.TwoSelects = false;
			}
			this.cancelToSelect();
		},
		cancelpart3() {
			//取消按钮
			if (this.showWhich == 8 || this.showWhich == 9 || this.showWhich == 11 || this.showWhich == 12) {
				//第8题和第9题
				let arr = this.forms['question' + this.showWhich]
				arr[arr.length - 1][3] = (this.showWhich == 8 || this.showWhich == 11) ? `定时某一点开；` : `定时某一点关；`
				arr.pop()
			} else if (this.showWhich == 101 || this.showWhich == 102 || this.showWhich == 103) {
				this.forms.question10 = null
				this.TwoSelects = false;
			} else if (this.showWhich == 131 || this.showWhich == 132 || this.showWhich == 133) {
				this.forms.question13 = null
				this.TwoSelects = false;
			}
			this.cancelToSelect();
		},
		//多选温度，滑动选择
		onWenduChange(picker, values) {
			this.values = [values[0] || '10℃', values[1] || '10℃']
		},
		//单选时间和温度
		onTimeChange(picker, values) { //选择的时候
			this.values = values[0] || '00'
		},
		selectsShow(num, val) { //弹框显示出来
			if (val) {
				this.TimeOrWendu[0].values.length = 0
				if (num == 8 || num == 9 || num == 11 || num == 12) { //第8题或者第9题
					for (let i = 0; i < 24; i++) {
						this.TimeOrWendu[0].values.push(tools.add0(i))
					}
					this.showWhich = num
					this.istimeselectshow = true;
				} else if (num == 101) { //选了第10题第一个选项
					for (let i = 10; i <= 30; i++) {
						this.TimeOrWendu[0].values.push(tools.add0(i) + '℃')
					}
					this.showWhich = num
					this.istimeselectshow = true;
				} else if (num == 102) {
					this.showWhich = num
					this.TwoSelects = true
				} else if (num == 103) {
					this.showWhich = num
					this.TwoSelects = true
				} else if (num == 131) { //选了第10题第一个选项
					for (let i = 10; i <= 30; i++) {
						this.TimeOrWendu[0].values.push(tools.add0(i) + '℃')
					}
					this.showWhich = num
					this.istimeselectshow = true;
				} else if (num == 132) {
					this.showWhich = num
					this.TwoSelects = true
				} else if (num == 133) {
					this.showWhich = num
					this.TwoSelects = true
				}
			}
		},
		//第三部分单选 对应question10
		otherradio(val, num) {
			let key = Object.keys(val)[0]
			if (this.currentpage == 3) {
				if (key == 0) {
					console.log('其他')
				} else if (key == 1) {
					this.selectsShow(101, true)
				} else if (key == 2) {
					this.selectsShow(102, true)
				} else if (key == 3) {
					this.selectsShow(103, true)
				}
			} else if (this.currentpage == 4) {
				if (key == 0) {
					console.log('其他')
				} else if (key == 1) {
					this.selectsShow(131, true)
				} else if (key == 2) {
					this.selectsShow(132, true)
				} else if (key == 3) {
					this.selectsShow(133, true)
				}
			}

			// this.istimeselectshow=true
		},
		calcForm(arr) {
			let question;
			for (let i = arr[0]; i <= arr[1]; i++) {
				question = this.forms['question' + i]
				if (Array.isArray(question) && !question.length) {
					return false
				} else if (typeof question == 'object' && !question) {
					return false
				}
			}
			return true
		},
		question1change(val) {
			this.formss.question1[val.num] = val.name
		},
		other(val, ev) {
			let input = ev.srcElement.parentNode.nextElementSibling.firstElementChild
			input.onblur = e => {
				let inputvalue = e.srcElement.value
				let inputname = e.srcElement.name
			}
			if (val) {
				input.focus();
			}
		},
		ceshi() {
			console.log(this.forms)
		},
		addcolor(page) {
			return this.currentpage >= page ? 'addcolor' : '';
		},
		previousstep() {
			//上一页
			if (this.currentpage == 1) {
				this.$router.replace('firstpart')
			} else {
				this.currentpage--
			}
		},
		nextstep() {
			if (this.toggleClass) {
				if (this.currentpage == this.allpage) {
					//提交数据
					MessageBox({
						title: '所有题都已经选择正确吗？提交结果后将无法修改。',
						showCancelButton: true,
						cancelButtonText: '返回修改',
						confirmButtonText: '确定，生成结果报告',
						confirmButtonClass: 'confirmButtonClass',
						cancelButtonClass: 'cancelButtonClass'
					}).then(action => {
						if (action == 'confirm') {
							addSecQuestionnaireAnswer(this.forms).then(res => {
								if (res.data.Result == "success") {
									Indicator.close();
									this.$router.replace({
										name: 'questionresult'
									})
								}else{
									Toast(res.data.message)
								}
							})
						} else {
							console.log('取消跳转了')
						}
					});
				} else {
					this.currentpage++
						this.$refs.question_wrap.scrollTop = 0;
				}
			} else {
				Toast('您还有题目没有选择')
			}
		},
		selectTime() {
			this.istimeselectshow = true
		},
		//第二题，选择时间
		onDateChange(picker, values) {
			values = values.map(item => {
				return item == undefined ? '00' : item
			})
			if (values[0] > values[2] || values[0] == values[2]) {
				picker.setSlotValue(2, values[0]);
				if (values[1] > values[3]) {
					picker.setSlotValue(3, values[1]);
				}
			}
			this.userSelecting = `${values[0]||'00'}:${values[1]||'00'} 至 ${values[2]||'00'}:${values[3]||'00'}`
		},
		sureToSelect() {
			this.forms.question2 = this.userSelecting
			this.istimeselectshow = false
		},
		cancelToSelect() {
			this.istimeselectshow = false
		},
	},
	created() {
		Indicator.close();
		this.timeselects.forEach(item => {
			if (!!item.values) {
				if (item.className == 'hours') {
					for (let i = 0; i < 24; i++) {
						item.values.push(tools.add0(i))
					}
				} else if (item.className == 'minutes') {
					for (let i = 0; i < 60; i++) {
						item.values.push(tools.add0(i))
					}
				}
			}
		})
		for (let i = 10; i <= 30; i++) {
			this.TwoWenDu[0].values.push(tools.add0(i) + '℃')
			this.TwoWenDu[2].values.push(tools.add0(i + 1) + '℃')
		}
	},
