<style lang="less">
@import '../../../styles/mixin';
.mint-indicator-wrapper {
	z-index: 200;
}

.mint-msgbox {
	.mint-msgbox-header {
		padding: 0;
	}
	#wh(518/@rem, 600/@rem);
	.mint-msgbox-title {
		margin: 105/@rem auto 0;
		width: 300/@rem;
		#sc(30/@rem, #333333);
		line-height: 45/@rem;
		font-weight: normal;
	}
	.mint-msgbox-btns {
		display: block;
		height: auto;
		margin-top: 60/@rem;
		.cancelButtonClass {
			border-right: none;
			display: block;
			#wh(330/@rem, 100/@rem);
			background-color: #D8D8D8;
			#sc(32/@rem, #333);
			margin: 0 auto 40/@rem;
		}
		.confirmButtonClass {
			display: block;
			#wh(330/@rem, 100/@rem);
			background-image: linear-gradient(174deg, #04A2FC 0%, #401CD5 100%);
			#sc(32/@rem, #fff);
			margin: 0 auto;
		}
	}
}
