<style lang="less">
@import '../../../styles/mixin';
@head_height: 120/@rem;
@btm_height : 105/@rem;
.second_part {
	height: 100%;
	overflow: hidden;
	.part_headers {
		position: fixed;
		background: #fff;
		top: 0;
		left: 0;
		z-index: 1;
		box-shadow: 1px 1px 1px #199ed8;
		#wh(100%, @head_height);
		#fj;
		padding-top: 30/@rem;
		h2 {
			font-size: 45/@rem;
			margin-left: 21/@rem;
		}
		.currentpage {
			#wh(267/@rem, @head_height);
			padding-right: 27/@rem;
			.textpage {
				text-align: right;
				padding-right: 6/@rem;
			}
			.progress {
				#fj(space-around);
				#wh(100%, 15/@rem);
				span {
					#wh(34/@rem, 100%);
					margin-top: 9/@rem;
					background: #f2f2f2;
				}
				.addcolor {
					background: #199ed8;
				}
			}
		}
	}
	.question_wrap {
		width: 100%;
		overflow-y: scroll;
		margin-top: @head_height + 20/@rem;
		height: calc(~"100%" - @head_height + 100/@rem);
		
		.el-radio__input {
			display: inline-block;
		}
		.el-radio-group {
			.el-radio {
				display: block;
				margin: 6/@rem 0;
			}
			.el-radio__label {
				display: inline-block;
				#sc(30/@rem, #666);
				padding-left: 24/@rem;
			}
		}
		.el-radio+.el-radio {
			margin-left: 9/@rem;
		}
		.el-checkbox {
			background: #fff;
		}
		h4 {
			margin-left: 30/@rem;
		}
		.options {
			width: 100%;
			#fj(space-around);
			padding-bottom: 21/@rem;
		}
		.question_content {
			width: 100%;
			li {
				width: 100%;
			}
		}
		.part1 {
			padding: 0 21/@rem;
			li {
				h4 {
					#sc(24/@rem, #333333);
					margin: 15/@rem 0;
				}
				.el-radio-group {
					#fj;
					flex-wrap: wrap;
					.el-radio:first-of-type{
						margin-left:9/@rem;
					}
				}
				.el-checkbox-group {
					#fj;
					align-items: center;
					padding: 0 21/@rem;
					flex-wrap: wrap;
					.el-checkbox {
						margin-left: 0;
						height: 66/@rem;
						line-height: 66/@rem;
					}
					.el-checkbox:last-of-type {
						margin-right: 120/@rem;
					}
				}
			}
			.selectTime {
				padding: 21/@rem 0;
				width: 354/@rem;
				padding-left: 27/@rem;
				border-bottom: 1px solid #797979;
			}
		}
		dl {
			dt {
				padding-left: 18/@rem;
				#sc(28/@rem, #333333);
				font-weight: bold;
				line-height: 51/@rem;
			}
			h4 {
				font-size: 27/@rem;
				line-height: 42/@rem;
				margin: 0 30/@rem;
			}
			dd {
				p {
					margin: 18/@rem 30/@rem;
					font-size: 27/@rem;
				}
			}
			.el-radio-group {
				margin: 0 30/@rem;
				.el-radio__label {
					padding-left: 18/@rem;
					line-height: 54/@rem;
				}
			}
			.el-checkbox-group {
				#fj;
				align-items: center;
				padding: 0 21/@rem;
				flex-wrap: wrap;
				.el-checkbox {
					margin-left: 0;
					height: 66/@rem;
					line-height: 66/@rem;
				}
				.el-checkbox:last-of-type {
					// position: absolute;
					margin-right: 120/@rem;
					width: 100%;
				}
			}
		}
		.part2 {
			h4 {
				line-height: 41/@rem;
			}
		}
		.part3,
		.part4 {
			dd {
				.el-radio {
					margin-left: 6/@rem;
					height: 2.2rem;
					line-height: 2.2rem;
				}
				.el-checkbox-group {
					margin-left: 15/@rem;
				}
				.el-checkbox {
					width: 50%;
				}
				.el-checkbox:last-of-type {
					// position: absolute;
					margin-right: 0;
				}
			}
		}
	}
	.setStep {
		#fj;
		#wh(750/@rem, 100/@rem);
		box-shadow: 0 0 4px 2px rgba(229, 229, 229, 1);
		position: relative;
		z-index: 200;
		background: #fff;
		text-align: center;
		line-height: @btm_height*8/10;
		div {
			flex: 1;
			#sc(33/@rem, #fff);
		}
		.previousstep {
			background: #ccc;
			border-right: 1px solid #fff;
		}
		.nextstep {}
		//下一步
		.nextstep.yes {
			background-image: linear-gradient(-174deg, #04A2FC 0%, #401CD5 100%);
		}
		.nextstep.no {
			background: #ccc;
		}
	}
}

.selecttimes {
	width: 100%;
}

.selecttimetool {
	#wh(100%, 100/@rem);
	#fj;
	border-bottom: 1px solid #199ed8;
	span {
		line-height: 100/@rem;
		display: inline-block;
		width: 150/@rem;
		height: 100%;
		text-align: center;
	}
}

.other {
	margin-left: 12/@rem;
	border: none; // width: 150/@rem;
	border-bottom: 1px solid #5a5e66;
}

.othershow {
	text-decoration: underline;
}

.origin {
	display: inline-block; // height:100%;
	transform: translateY(10/@rem);
	width: 150/@rem;
	border-bottom: 1px solid #5a5e66;
}

.other.no {
	border-bottom: 1px solid #5a5e66;
}

.other.yes {
	width: 150/@rem;
	border-bottom: 1px solid #199ed8;
	color: #199ed8;
}
</style>


